import React from 'react'
import { Link } from 'react-router-dom';
const logo =require('../adondeLogo.png')
export default function () {
    return (
        <header className="main-Header full grid-container">
            <div className="header flex align-center space-between">

            <Link to="/">
            <div className="logo-container">
            <img src={logo} alt=""/>
            </div>
            </Link>
            <input type="text" placeholder="Where do yo wanna go?"/>
            <ul className="flex clean-list">

                <li>
                    Home
                </li>
                <li>
                    About
                </li>
                <li>
                    Login
                </li>
            </ul>
            </div>
        </header>
    )
}
